* {
    box-sizing: border-box;
}

body {
    color: lightgray;
    background-color: black;
    min-height: 100vh;
}

header {
    display: flex;
    flex-direction: row;
    font-size: .75rem;
    align-items: center;
    padding: 20px 20px;
}

.header--image {
    width: 40px;
    max-width: 40px;
    height: 40px;
    filter: invert(100%);
}

.header--title {
    margin-left: 10px;
    margin-right: auto;
    font-family: Arial, Helvetica, sans-serif;
}

.main--title {
    text-align: center;
    font-family: 'Quicksand', sans-serif;
}

.main--sheep-jump-area {
    padding-top: 10px;
    padding-bottom: 40px;
    overflow: hidden;
    position: relative;
}

.counter-display {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 130px;
}

.main--sheep {
    width: 70px;
    height: 70px;
    filter: invert(80%);
    right: 42%;
    top: 60%;
    position: absolute;
    transform: translateX(100%);
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    z-index: 1;
}

.sheep-animate {
    animation-name: sheep-animation;
    animation-timing-function: ease-in-out;
}

@keyframes sheep-animation {
    0% {
      transform: translateX(100%) translateY(0%);
    }
    50% {
      transform: translateX(0%) translateY(-100%);
      animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
    }
    100% {
      transform: translateX(-100%) translateY(0%);
    }
}
  
.main--fence {
    width: 40px;
    filter: invert(70%);
    display: block;
    margin: 0 auto;
}

.main--buttons-container {
    display: grid;
    grid: auto/ auto auto;
    padding: 0px 24px;
    gap: 5px;
} 

.main--buttons {
    background-color: lightgray;
    color: black;
    padding: 20px;
    border: none;
    border-radius: 7px;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 600;
    font-size: 1rem;
    cursor: pointer;
}

.main--buttons:hover {
    filter: brightness(80%);
    transition: ease 250ms;
}

footer {
    font-family: 'Source Sans Pro';
    font-size: .75rem;
    font-weight: light;
    text-align: center;
    margin-top: 110px;
    padding-top: 15px;
    padding-bottom: 8px;
    position: relative;
    bottom: 0;
    left: 0;
    width: 100%;
    color: lightgray
}

.footer-icons {
    width: 23px;
    margin-right: 13px;
    margin-bottom: 3px;
    filter: invert(70%);
}

.footer-icons:hover{
    filter: invert(30%);
}

footer > p {
    line-height: .5;
}

@media screen and (min-width: 500px){
    .main--sheep {
        right: 46%;
    }

    .main--buttons-container {
        padding: 0 200px;
    }

    footer{
        position: fixed;
    }
}

@media screen and (min-width: 1025px){
    header {
    padding: 20px 300px;
    }

    .main--sheep-jump {
        padding-block: 100px;
        color: lightgray;
    }

    .main--title {
        font-size: 2.5rem;
    }

    .main--sheep {
        right: 48%;
    }

    .main--buttons-container {
        padding: 0px 800px;
    }

}